<template>
  <div ref="wrapper">
    <slot></slot>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'
  export default {
    props: {
      /**
       * 1 scroll
       * 2 scroll
       * 3 scrollswipescroll
       */
      probeType: {
        type: Number,
        default: 1
      },
      /**
       * click
       */
      click: {
        type: Boolean,
        default: true
      },
      /**
       * 
       */
      scrollX: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      listenScroll: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      data: {
        type: Array,
        default: null
      },
      /**
       * 
       */
      pullup: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      pulldown: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      beforeScroll: {
        type: Boolean,
        default: false
      },
      /**
       * scroll
       */
      refreshDelay: {
        type: Number,
        default: 20
      }
    },
    mounted() {
      // DOMbetter-scroll
      setTimeout(() => {
        this._initScroll()
      }, 20)
    },
    methods:{
      _initScroll(){
        if(!this.$refs.wrapper){
          return
        }
        this.scroll = new BScroll(this.$refs.wrapper,{
          probeType:this.probeType,
          click:this.click
        })
        if(this.listenScroll){
          let me = this
          this.scroll.on('scroll',(pos)=>{
            // pos 
            me.$emit('scroll',pos)
          })
        }
        if(this.pullup){
          // 
          let me = this
          this.scroll.on('scrollEnd',()=>{
            if(this.scroll.y <= (this.scroll.maxScrollY + 50)){
              // 
              me.$emit('scrollToEnd')
            }
          })
        }
        if(this.beforeScroll){
          // 
          this.scroll.on('beforeScrollStart',()=>{
            this.$emit('beforeScroll')
          })
      }
    },
    enable(){
        this.scroll && this.scroll.enable()
      },
      disable() {
        this.scroll && this.scroll.disable()
      },
      refresh() {
        this.scroll && this.scroll.refresh()
      },
      scrollTo(){
        this.scroll && this.scroll.scrollTo.apply(this.scroll,arguments)
      },
      scrollToElement(){
        // scrollToElement(el, time, offsetX, offsetY, easing)
        this.scroll && this.scroll.scrollToElement.apply(this.scroll,arguments)
      }
    },
    watch:{
      data(){
        setTimeout(()=>{
          this.refresh()
        },this.refreshDelay)
      }
    }
  }
</script>
<style lang="stylus" ref="stylesheet/stylus">

</style>



